.program-form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @media (min-width: $tablet-width) {
    margin-bottom: 72px;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 82px;
  }
}

.program-form__sub-title {
  width: 87.5%;

  font-weight: normal;
  font-size: inherit;
  line-height: inherit;
  font-family: $font-stack;
  color: $titel-lightblack;
  text-transform: uppercase;

  @include header-reset($margin: 0 auto 25px, $padding: 0 20px 0 0);

  @media (min-width: $tablet-width) {
    width: 81.7%;
    margin-bottom: 52px;
    padding-right: 250px;

    font-size: 20px;
    line-height: 30px;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
    max-width: 1300px;
    margin: 0 auto;
    margin-bottom: 52px;
    padding: 0 900px 0 40px;
  }
}

.program-form fieldset {
  margin: 14px 0 0 0;
  padding: 0;

  border: 0;

  @media (min-width: $desktop-width) {
    padding: 0;
  }
}

fieldset.program-form__checklist {
  @media (min-width: $tablet-width) {
    margin-bottom: 11px;
  }

  @media (min-width: $desktop-width) {
    width: 570px;
    margin: 15px 40px 0 auto;
  }
}

.program-form__checklist .program-form__list {
  align-content: space-between;
  height: 177px;

  @media (min-width: $tablet-width) {
    height: 216px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
  }
}

.program-form__checklist label {
  margin-right: 14px;
}

.program-form__checklist input {
  padding: 13px 16px 12px;
}

.program-form__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @include list-reset(
  $list-style-type: none,
  $margin: 0 auto,
  $padding: 0 6.25%
  );

  @media (min-width: $tablet-width) {
    padding: 0 8.55% 0 9.2%;
  }

  @media (min-width: $desktop-width) {
    padding: 0 2.55% 0 6.25%;
  }
}

.program-form__additionally .program-form__list {
  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
  }

  @media (min-width: $desktop-width) {
    padding: 0 40px;
  }
}

fieldset.program-form__additionally {
  margin: 0;
  margin-top: 26px;

  &::before {
    content: "";
    position: relative;
    top: 17px;

    display: block;
    width: 100%;
    height: 3px;

    background-image: linear-gradient(90deg, $white, $white, $green 155px);

    @media (min-width: $tablet-width) {
      top: 25px;

      background-image: linear-gradient(90deg, $green 10%, $green 322px);
    }

    @media (min-width: $desktop-width) {
      top: 25px;

      background-image: linear-gradient(90deg, $green 10%, $green 292px);
    }
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 2px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 73px;
  }
}

.program-form__additionally .program-form__title {
  width: 164px;
  margin-bottom: 37px;
  padding-right: 24px;

  @media (min-width: $tablet-width) {
    width: 291px;
    margin-bottom: 65px;
    padding-right: 41px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 65px;
    margin-left: 0;
  }
}

.program-form__additionally .program-form__item {
  @media (min-width: $tablet-width) {
    width: 50%;
    margin: 0 -27px 52px 0;
  }

  @media (min-width: $desktop-width) {
    width: 245px;
    margin: 0 40px;

    &:first-child {
      margin-right: 40px;
      margin-left: 0;
    }

    &:last-child {
      margin-right: 0;
      margin-left: 40px;
    }
  }
}

.program-form__item {
  position: relative;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 13px;

  &--radio,
  &--checkbox {
    position: relative;

    margin-top: 4px;
    margin-bottom: 20px;

    &:active,
    &:focus {
      outline: $outline solid 2px;
    }

    @media (min-width: $tablet-width) {
      margin-top: 2px;
      margin-bottom: 17px;
    }
  }

  &--radio input,
  &--checkbox input {
    width: 0;
    margin: 0;
    padding: 0;

    background: none;
    opacity: 0;

    appearance: none;
  }

  &--radio label,
  &--checkbox label {
    width: 100%;
    padding-left: 46px;

    text-align: left;

    &::before {
      content: "";
      position: absolute;
      top: -6px;
      left: 1px;

      width: 30px;
      height: 30px;

      border: 1px solid $border-grey;
      border-radius: 50%;

      @media (min-width: $tablet-width) {
        top: -8px;

        width: 40px;
        height: 40px;
      }
    }

    &:hover::before {
      border-color: $border-two;
    }

    &:active {
      opacity: 0.3;
    }

    @media (min-width: $tablet-width) {
      padding-left: 59px;
    }
  }

  &--radio {
    @media (min-width: $tablet-width) {
      margin-bottom: 32px;
    }
  }

  &--radio input:checked + label::after {
    content: "";
    position: absolute;
    top: 4px;
    left: 11px;

    width: 10px;
    height: 10px;

    background-color: $green;
    border-radius: 50%;

    @media (min-width: $tablet-width) {
      top: 4px;
      left: 13px;

      width: 16px;
      height: 16px;
    }
  }

  &--checkbox label {
    position: relative;

    &::before {
      border-radius: 0;
    }
  }

  &--checkbox svg {
    display: none;
  }

  &--checkbox input:checked {
    &::after {
      background: none;
      border-radius: 0;
    }
  }

  &--text {
    @media (min-width: $tablet-width) {
      margin-bottom: 20px;
    }
  }

  &--text input {
    width: 100%;
    height: 46px;
    padding: 7px 16px;

    // Fix placeholder color in Firefox 19+ and Firefox 18-
    &::-moz-placeholder,
    &:-moz-placeholder {
      opacity: 1;
    }

    @media (min-width: $tablet-width) {
      height: 52px;
      padding: 7px 20px;
    }
  }

  &--text label {
    margin-right: 14px;

    @media (min-width: $tablet-width) {
      margin-right: 17px;

      font-size: 20px;
      line-height: 20px;
    }
  }
}

.program-form__input-radio:focus ~ label,
.program-form__input-chekbox:focus ~ label {
  outline: $outline solid 2px;
}

.program-form__input-chekbox:checked + label svg {
  position: absolute;
  top: 0;
  left: 6px;

  display: block;
  width: 21px;
  height: 16px;

  fill: $green;

  @media (min-width: $tablet-width) {
    top: 3px;
    left: 10px;
  }
}

.program-form__item label {
  flex-shrink: 0;

  font-size: 16px;
  line-height: inherit;
  font-family: $font-stack;
  color: $titel-black-two;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 20px;
  }
}

.program-form__item input {
  position: relative;

  font-size: 16px;
  line-height: inherit;
  font-family: $font-stack;
  color: $text-darkgrey;
  text-transform: uppercase;

  border: 0;
  outline: none;
  box-shadow: inset 0 0 0 1px transparentize($border-form, 0.5);

  &:hover {
    box-shadow: inset 0 0 0 1px $border-form;
  }

  &::before {
    content: "";
    position: absolute;
  }

  &:active,
  &:focus {
    box-shadow: inset 0 0 0 2px $text-darkgrey;
  }

  &:invalid:not(:placeholder-shown),
  &:focus:invalid {
    box-shadow: inset 0 0 0 2px $error;
  }

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 20px;

    &:active,
    &:focus {
      box-shadow: inset 0 0 0 2px $text-darkgrey;
    }
  }
}

.program-form__item input::placeholder {
  color: $text-darkgrey;
}

fieldset.program-form__options {
  margin-top: 9px;
  padding: 22px 0 8px 0;

  border-top: 1px solid $border-darkwhite;
  border-right: 0;
  border-bottom: 1px solid $border-darkwhite;
  border-left: 0;

  .program-form__list {
    @media (min-width: $tablet-width) {
      padding: 0;
    }
  }

  @media (min-width: $tablet-width) {
    width: 81.9%;
    margin: 41px auto 49px;
    padding: 30px 0 0 28px;

    border: 1px solid $border-darkwhite;
  }

  @media (min-width: $desktop-width) {
    width: 570px;
    margin: 15px auto 29px 40px;
    padding: 29px 0 2px 27px;

    border-top: 1px solid $border-darkwhite;
    border-right: 1px solid $border-darkwhite;
    border-bottom: 1px solid $border-darkwhite;
    border-left: 1px solid $border-darkwhite;
  }
}

fieldset.program-form__contacts {
  margin: 0;
  margin-top: 25px;

  &::before {
    content: "";
    position: relative;
    top: 17px;

    display: block;
    width: 100%;
    height: 3px;

    background-image: linear-gradient(90deg, $white, $white, $green 221px);

    @media (min-width: $tablet-width) {
      top: 25px;

      background-image: linear-gradient(90deg, $green 10%, $green 221px);
    }

    @media (min-width: $desktop-width) {
      top: 24px;

      background-image: linear-gradient(90deg, $green 10%, $green 221px);
    }
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 31px;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
    margin-top: 38px;
    margin-bottom: 22px;

    .program-form__list {
      flex-direction: row;
      padding: 0 44px;
    }

    .program-form__item {
      width: 570px;
    }
  }

  .program-form__title {
    margin-bottom: 34px;

    @media (min-width: $tablet-width) {
      margin-bottom: 76px;
      margin-left: 30px;
    }

    @media (min-width: $desktop-width) {
      margin-left: 0;
    }
  }
}

.program-form__contacts label {
  margin-right: 10px;

  @media (min-width: $tablet-width) {
    margin-right: 16px;
  }
}

.program-form__contacts input[name="user-email"] {
  &:active + svg,
  &:focus + svg {
    fill: $green;
  }

  &:invalid:not(:placeholder-shown) + svg,
  &:focus:invalid + svg {
    fill: $error;
  }

  @media (min-width: $tablet-width) {
    padding: 9px 50px 11px 23px;

    &:active,
    &:focus {
      box-shadow: inset 0 0 0 2px $text-darkgrey;
    }

    &:invalid:not(:placeholder-shown),
    &:focus:invalid {
      box-shadow: inset 0 0 0 2px $error;
    }
  }

  @media (min-width: $desktop-width) {
    padding-left: 20px;

    &:active,
    &:focus {
      box-shadow: inset 0 0 0 2px $text-darkgrey;
    }

    &:invalid:not(:placeholder-shown),
    &:focus:invalid {
      box-shadow: inset 0 0 0 2px $error;
    }
  }
}

.program-form__contacts input[name="user-phone"] {
  &:active + svg,
  &:focus + svg {
    fill: $green;
  }

  &:invalid:not(:placeholder-shown) + svg,
  &:focus:invalid + svg {
    fill: $error;
  }

  @media (min-width: $tablet-width) {
    padding: 9px 50px 11px 16px;

    &:active,
    &:focus {
      box-shadow: inset 0 0 0 2px $text-darkgrey;
    }

    &:invalid:not(:placeholder-shown),
    &:focus:invalid {
      box-shadow: inset 0 0 0 2px $error;
    }
  }

  @media (min-width: $desktop-width) {
    padding-left: 20px;

    &:active,
    &:focus {
      box-shadow: inset 0 0 0 2px $text-darkgrey;
    }

    &:invalid:not(:placeholder-shown),
    &:focus:invalid {
      box-shadow: inset 0 0 0 2px $error;
    }
  }
}

.program-form__contacts input[name="user-email"] + svg {
  position: absolute;
  right: 16px;

  @media (min-width: $desktop-width) {
    right: 23px;
  }
}

.program-form__contacts input[name="user-phone"] + svg {
  position: absolute;
  right: 12px;

  @media (min-width: $tablet-width) {
    right: 16px;
  }

  @media (min-width: $desktop-width) {
    right: 15px;
  }
}

.program-form__wrapper {
  @media (min-width: $desktop-width) {
    max-width: 1300px;
    margin: 0 auto;
  }
}

.program-form__message,
.program-form__additionally {
  @media (min-width: $desktop-width) {
    width: 100%;
  }
}

fieldset.program-form__message {
  margin: 0;
  margin-top: 11px;

  &::before {
    content: "";
    position: relative;
    top: 17px;

    display: block;
    width: 100%;
    height: 3px;

    background-image: linear-gradient(90deg, $white, $white, $green 155px);

    @media (min-width: $tablet-width) {
      top: 25px;

      background-image: linear-gradient(90deg, $green 10%, $green 289px);
    }

    @media (min-width: $desktop-width) {
      top: 35px;

      background-image: linear-gradient(90deg, $green 10%, $green 260px);
    }
  }

  .program-form__title {
    width: 142px;
    padding-right: 24px;

    @media (min-width: $tablet-width) {
      width: 259px;
      margin-bottom: 76px;
      padding-right: 39px;
    }

    @media (min-width: $desktop-width) {
      margin-top: 10px;
      margin-bottom: 75px;
      margin-left: 0;
    }
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 39px;
  }
}

.program-form__title {
  position: relative;

  width: 200px;
  margin: 0 77px 34px 6.25%;
  padding: 0;

  font-weight: normal;
  font-size: 24px;
  line-height: 24px;
  font-family: $font-stack;
  color: $titel-lightblack;

  background-color: $white;

  @media (min-width: $tablet-width) {
    width: 593px;
    margin-bottom: 59px;
    margin-left: 3.9%;
    padding-right: 42px;
    padding-left: 39px;

    font-size: 36px;
    line-height: 36px;
  }
}

.program-form__comment {
  display: block;
  width: 87.5%;
  height: 150px;
  margin: 0 auto;
  padding: 13px 0 9px 14px;

  font-size: 16px;
  line-height: inherit;
  font-family: $font-stack;
  color: $text-darkgrey;
  text-transform: uppercase;

  border: 1px solid $border-grey;

  &:focus {
    outline: $outline solid 2px;
  }

  @media (min-width: $tablet-width) {
    width: 82%;
    height: 174px;
    margin-bottom: 36px;
    padding: 9px 0 0 19px;

    font-size: 20px;
    line-height: 30px;
  }

  @media (min-width: $desktop-width) {
    width: 1220px;
    margin: 0 40px;
  }
}

.program-form__button-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 6.25%;
  margin-bottom: 31px;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 9.25%;
  }

  @media (min-width: $desktop-width) {
    max-width: 1300px;
    margin: 0 auto;
  }
}

.program-form__button {
  margin-top: 21px;
  padding: 10px;

  font-size: 16px;
  line-height: 20px;

  border: none;
  outline: none;
  cursor: pointer;

  @media (min-width: $tablet-width) {
    width: 45.2%;
    margin-top: 12px;
    padding: 16px;

    font-size: 20px;
  }

  @media (min-width: $desktop-width) {
    width: 570px;
    margin-top: 0;
    margin-right: 40px;
  }
}

.program-form__legend {
  margin: 16px auto 1px;

  font-size: inherit;
  line-height: 16px;

  sup {
    position: relative;
    top: 5px;
  }

  @media (min-width: $tablet-width) {
    width: 45.5%;
    margin-top: 23px;
    padding-left: 33px;

    font-size: 16px;
  }

  @media (min-width: $desktop-width) {
    width: 570px;
    margin-top: 11px;
    margin-left: 40px;
    padding: 0;
  }
}
