.main-nav {
  position: relative;
  z-index: 1;

  width: auto;
  min-height: 0;

  @media (min-width: $tablet-width) {
    width: 57.29%;
    margin-right: 3.9%;
  }

  @media (min-width: $desktop-width) {
    width: 36.92%;
    margin-right: 3.1%;
  }
}

.main-nav__toggle {
  position: absolute;
  top: -40px;
  right: 6.25%;
  z-index: 2;

  display: block;
  width: 24px;
  height: 18px;
  padding: 0;

  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;

  width: 24px;
  height: 2px;

  background-color: $titel-black-two;
  box-shadow: 0 6px 0 0 $titel-black-two, 0 12px 0 0 $titel-black-two;
}

.main-nav--closed .main-nav__list {
  @media (max-width: $mobile-width-only) {
    display: none;
  }
}

.main-nav--opened .main-nav__toggle {
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 8px;
    left: 0;

    width: 24px;
    height: 2px;

    background-color: $titel-black-two;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.main-nav--nojs .main-nav__toggle {
  @media (max-width: $mobile-width-only) {
    display: none;
  }
}
