.programs {
  margin-bottom: 17px;
}

.programs__wrapper {
  width: auto;

  @media (min-width: $desktop-width) {
    max-width: 1300px;
    margin: 0 auto;
  }
}

.programs__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @include list-reset($list-style-type: none, $margin: 0 6.25%, $padding: 0);

  @media (min-width: $tablet-width) {
    margin: 0 3.9%;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    margin: 0 3.11%;
  }
}

.programs__item {
  z-index: 1;

  margin-bottom: 20px;
  padding: 20px 20px 19px 20px;

  background-color: $bg-white-three;

  @media (min-width: $tablet-width) {
    margin-bottom: 30px;
    padding: 66px 62px 59px 52px;
  }

  @media (min-width: $desktop-width) {
    width: 46.7%;
    margin-bottom: 0;
    padding: 50px;
    padding-bottom: 60px;
  }

  &--slim .programs__title::after {
    content: "";
    position: absolute;
    top: 1px;
    left: 2px;

    width: 50px;
    height: 50px;

    background: url("../img/icon-cat-slim.svg") center no-repeat;

    @media (min-width: $tablet-width) {
      top: 14px;
      right: 3px;
      left: auto;

      width: 194px;
      height: 194px;
    }

    @media (min-width: $desktop-width) {
      top: -9px;
      right: auto;
      left: 12px;

      width: 80px;
      height: 97px;
    }
  }

  &--pro {
    margin-bottom: 0;
  }

  &--pro .programs__title::after {
    content: "";
    position: absolute;
    top: 4px;
    left: -7px;

    width: 67px;
    height: 50px;

    background: url("../img/icon-cat-pro.svg") center no-repeat;

    @media (min-width: $tablet-width) {
      top: -9px;
      right: -32px;
      left: auto;

      width: 268px;
      height: 268px;
    }

    @media (min-width: $desktop-width) {
      top: -2px;
      right: auto;
      left: -15px;

      width: 134px;
      height: 97px;
    }
  }

  &--slim .programs__description {
    @media (min-width: $tablet-width) {
      margin-right: 37%;
    }
  }

  &--pro .programs__description {
    @media (min-width: $tablet-width) {
      margin-right: 42.5%;
    }
  }
}

.programs__title {
  position: relative;

  font-weight: normal;
  font-size: 24px;
  line-height: 37px;
  font-family: $font-stack;
  color: $titel-lightblack;
  text-transform: uppercase;

  @include header-reset($margin: 0, $padding: 10px 0 15px 71px);

  &::before {
    content: "";
    position: absolute;
    top: 4px;
    left: 2px;

    width: 50px;
    height: 50px;

    background-color: $green;
    border-radius: 50%;
  }

  @media (min-width: $tablet-width) {
    padding-bottom: 10px;
    padding-left: 0;

    font-size: 36px;

    &::before {
      top: 26px;
      right: 0;
      left: auto;

      width: 200px;
      height: 200px;
    }
  }

  @media (min-width: $desktop-width) {
    padding: 26px 0 0 164px;

    &::before {
      top: -3px;
      right: auto;
      left: 3px;

      width: 100px;
      height: 100px;
    }
  }
}

.programs__description {
  font-size: 14px;
  line-height: 18px;

  border-bottom: 1px solid $border-lightgrey;

  @include header-reset($margin: 0 0 19px 0, $padding: 14px 0 22px 0);

  @media (min-width: $tablet-width) {
    margin-bottom: 35px;

    font-size: 16px;
    line-height: 24px;
  }

  @media (min-width: $desktop-width) {
    width: 462px;
    margin-bottom: 10px;
    padding-top: 73px;
  }
}

.programs__link {
  position: relative;

  font-size: 16px;
  line-height: 16px;
  font-family: $font-stack;
  color: $titel-lightblack;
  text-transform: uppercase;

  transition: 0.3s;

  &:active,
  &:focus {
    opacity: 0.3;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;

    width: 24px;
    height: 12px;

    background: url("../img/icon-right-arrow-long.svg") center no-repeat;
    transform: translateY(-50%);

    transition: 0.3s;
  }

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 20px;
  }
}

.programs__item--slim .programs__link::after {
  left: 97px;

  @media (min-width: $tablet-width) {
    left: 132px;
  }

  @media (min-width: $desktop-width) {
    left: 134px;
  }
}

.programs__item--pro .programs__link::after {
  left: 95px;

  @media (min-width: $tablet-width) {
    left: 126px;
  }

  @media (min-width: $desktop-width) {
    left: 128px;
  }
}

.programs__link:hover::after,
.programs__link:active::after,
.programs__link:focus::after {
  width: 34px;

  background-image: url("../img/icon-right-arrow-verylong.svg");
}
