.catalog-to-all {
  position: relative;

  background-color: transparent;
  border-bottom: 1px solid $border-darkwhite;

  @media (min-width: $tablet-width) {
    border: 1px solid $border-form;
  }
}

.catalog-to-all::before {
  content: "";
  position: absolute;
  top: 26px;
  left: calc(50% - 30px / 2);

  width: 30px;
  height: 30px;

  background-image: url("../img/icon-plus-mobile.svg");
  opacity: 0.2;

  @media (min-width: $tablet-width) {
    top: 54px;
    left: calc(50% - 60px / 2);

    width: 60px;
    height: 60px;

    background-image: url("../img/icon-plus.svg");
  }
}

.catalog-to-all__title,
.catalog-to-all__link {
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  font-family: $font-stack;
  text-align: left;
  color: $titel-lightblack;
  text-transform: uppercase;
}

.catalog-to-all__title {
  position: relative;

  width: 100%;
  margin: 0 5.97%;
  padding-top: 80px;

  text-align: center;

  @media (min-width: $tablet-width) {
    margin: 0;
    padding: 164px 27% 0 27%;

    font-size: 20px;
  }

  @media (min-width: $desktop-width) {
    padding: 164px 22% 0 22%;
  }
}

.catalog-to-all__link {
  width: 100%;
  margin: 0 5.97% 24px;
  padding: 10px 0;

  text-align: center;
  color: $text-darkgrey;

  background-color: $bg-white-three;

  &:hover {
    background-color: $bg-white-seven;
  }

  @media (min-width: $tablet-width) {
    width: 77.65%;
    margin: 0 auto 38px auto;

    font-size: 16px;
    line-height: 20px;
  }

  @media (min-width: $desktop-width) {
    width: 169px;
  }
}

.catalog-to-all__description {
  width: 100%;
  margin: 0 5.97%;
  padding: 7px 0 21px;

  font-size: 12px;
  line-height: 16px;
  text-align: center;
  color: inherit;

  @media (min-width: $tablet-width) {
    margin: 0;
    margin-top: -60px;
    padding: 0 24%;

    font-size: 16px;
    color: $text-darkgrey;
  }

  @media (min-width: $desktop-width) {
    padding: 0 14%;
  }
}
