.button {
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  font-family: $font-stack;
  text-align: center;
  color: $white;
  text-transform: uppercase;

  background-color: $green;

  &:hover {
    background-color: $grass;
  }

  &:active span,
  &:focus span {
    opacity: 0.3;
  }
}
