.program__title {
  font-weight: normal;
  font-size: 36px;
  line-height: 60px;
  font-family: $font-stack;
  color: $titel-lightblack;

  @include header-reset($margin: 15px auto 25px, $padding: 0 6.25%);

  @media (min-width: $tablet-width) {
    margin-top: 58px;
    margin-bottom: 55px;
    padding: 0 3.75%;

    font-size: 60px;
  }

  @media (min-width: $desktop-width) {
    max-width: 1300px;
    margin: 0 auto;
    margin-top: 75px;
    margin-bottom: 55px;
    padding: 0 40px;
  }
}
