.copyright {
  position: relative;
}

.copyright__link {
  padding: 10px 47px 10px 0;

  font-size: 16px;
  line-height: 16px;
  text-align: left;
  color: inherit;

  &:hover .copyright__svg {
    fill: $green;
  }

  &:active .copyright__svg,
  &:focus .copyright__svg {
    opacity: 0.3;

    fill: $green;
  }
}

.copyright__svg {
  position: absolute;
  top: calc(50% - 41px / 2);
  right: -8px;

  width: 41px;

  fill: $svg-lightgrey;
}
