.how-it-works {
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    margin-top: 43px;
    margin-bottom: 27px;
    padding-bottom: 9px;

    border-bottom: 1px solid $border-darkwhite;
  }

  @media (min-width: $desktop-width) {
    margin-top: 0;
    margin-bottom: 100px;

    border: 0;
  }
}

.how-it-works__wrapper {
  width: auto;
  margin: 0 6.25%;

  @media (min-width: $tablet-width) {
    margin: 0 3.9%;
  }

  @media (min-width: $desktop-width) {
    max-width: 1300px;
    margin: 0 auto;
  }
}

.how-it-works__title {
  font-weight: normal;
  font-size: 36px;
  line-height: 60px;
  font-family: $font-stack;
  text-align: left;
  color: $titel-lightblack;

  @include header-reset($margin: 0 auto 30px auto, $padding: 0);

  @media (min-width: $tablet-width) {
    font-size: 60px;
  }

  @media (min-width: $desktop-width) {
    margin: 70px 3.07% 0 3.07%;
  }
}

.how-it-works__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @include list-reset;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
  }

  @media (min-width: $desktop-width) {
    margin: 30px 3.07% 0;
  }
}

.how-it-works__item {
  position: relative;

  margin-bottom: 26px;

  &--leaf {
    .how-it-works__description::after {
      background: url("../img/advantage-leaf@1x.png");

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/advantage-leaf@2x.png");
        background-repeat: no-repeat;
        background-size: 100% auto;
      }
    }

    @media (min-width: $tablet-width) {
      &::after {
        content: "1";
        top: 1px;
        left: 118px;
      }
    }
  }

  &--water {
    .how-it-works__description::after {
      background: url("../img/advantage-powder@1x.png");

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/advantage-powder@2x.png");
        background-repeat: no-repeat;
        background-size: 100% auto;
      }
    }

    @media (min-width: $tablet-width) {
      &::after {
        content: "2";
        top: 3px;
        left: 111px;
      }
    }
  }

  &--cookware {
    .how-it-works__description::after {
      background: url("../img/advantage-dining@1x.png");

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/advantage-dining@2x.png");
        background-repeat: no-repeat;
        background-size: 100% auto;
      }
    }

    @media (min-width: $tablet-width) {
      &::after {
        content: "3";
        top: 3px;
        left: 113px;
      }
    }
  }

  &--watch {
    .how-it-works__description::after {
      background: url("../img/advantage-clock@1x.png");

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/advantage-clock@2x.png");
        background-repeat: no-repeat;
        background-size: 100% auto;
      }
    }

    @media (min-width: $tablet-width) {
      &::after {
        content: "4";
        top: 2px;
        left: 113px;
      }
    }
  }

  @media (min-width: $tablet-width) {
    width: 37%;
    margin-right: 8.8%;
    margin-bottom: 12.9%;

    &::after {
      position: absolute;
      z-index: -1;

      font-weight: normal;
      font-size: 281px;
      line-height: 281px;
      font-family: "Oswald", "Arial", sans-serif;
      color: $bg-white-three;
    }
  }

  @media (min-width: $desktop-width) {
    width: 20.08%;
    margin: 0;
  }
}

.how-it-works__description {
  width: auto;
  min-height: 54px;
  margin: 0;
  padding-left: 80px;

  font-size: 14px;
  line-height: 18px;
  text-align: left;
  color: inherit;

  &::before {
    content: "";
    position: absolute;
    top: -2px;
    left: 0;

    width: 60px;
    height: 60px;

    background-color: $green;

    @media (min-width: $tablet-width) {
      top: 42px;

      width: 80px;
      height: 80px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    top: 3px;
    left: 5px;

    width: 50px;
    height: 50px;

    @media (min-width: $tablet-width) {
      top: 57px;
      left: 15px;
    }
  }

  @media (min-width: $tablet-width) {
    width: 100%;
    padding-top: 154px;
    padding-left: 0;

    font-size: 16px;
    line-height: 24px;
  }
}

.how-it-works__description span {
  @media (min-width: $tablet-width) {
    display: block;
  }
}
