.catalog {
  max-width: $desktop-width;
  margin: 0 auto;
}

.catalog__wrapper {
  width: auto;

  @media (min-width: $tablet-width) {
    margin: 0 3.9%;
  }

  @media (min-width: $desktop-width) {
    margin: 0 3.07%;
  }
}

.catalog__title {
  margin: 15px 6.25% 28px 6.25%;

  font-weight: normal;
  font-size: 36px;
  line-height: 60px;
  font-family: $font-stack;
  color: $titel-lightblack;

  @media (min-width: $tablet-width) {
    margin: 58px 0 25px 0;

    font-size: 60px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 75px;
    margin-left: 0;
  }
}

.catalog__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @include list-reset($list-style-type: none, $margin: 0 auto, $padding: 0);

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.catalog__item {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;

  border-top: 1px solid $border-darkwhite;
  border-right: 1px solid $border-darkwhite;
  border-left: 1px solid $border-darkwhite;

  @media (min-width: $tablet-width) {
    flex-direction: column;
    width: 45.75%;
    max-width: 400px;
    margin-top: 140px;

    background-color: $bg-white-three;
  }

  @media (min-width: $desktop-width) {
    width: 20.08%;
  }
}

.catalog__product-link,
.catalog__product-description {
  width: 44.03%;
}

.catalog__product-link {
  margin-left: 5.97%;

  @media (min-width: $tablet-width) {
    position: relative;
    top: -38px;
    left: -3px;

    width: 100%;
    height: 163px;
    margin: 0;
  }
}

.catalog__image-paste {
  @media (min-width: $tablet-width) {
    position: absolute;
    bottom: 0;

    width: 100%;
  }
}

.catalog__product-title {
  width: 80px;
  margin: 21px 0 12px 0;

  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  font-family: $font-stack;
  text-align: left;
  color: $titel-lightblack;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    width: 130px;
    margin: 0 auto 15px auto;

    font-size: 20px;
    text-align: center;
  }
}

.catalog__product-description {
  margin-right: 5.97%;

  @media (min-width: $tablet-width) {
    width: 100%;
    margin: 0;
    padding: 0 11.18%;
  }

  @media (min-width: $desktop-width) {
    padding: 0 15.5%;
  }
}

.catalog__cart-link {
  width: 100%;
  margin: -4px 5.97% 24px;
  padding: 10px 0;

  font-size: 16px;
  line-height: 20px;

  @media (min-width: $tablet-width) {
    width: 77.65%;
    margin: 27px auto 40px auto;
  }

  @media (min-width: $desktop-width) {
    width: 169px;
  }
}

.catalog__product-specifications {
  width: 100%;

  border-spacing: 0;

  tr {
    margin-bottom: 5px;
  }

  @media (min-width: $tablet-width) {
    padding-top: 5px;

    border-spacing: 0 7px;
  }
}

.catalog__field,
.catalog__value {
  padding: 0;

  font-size: 12px;
  line-height: 16px;
  color: inherit;

  @media (min-width: $tablet-width) {
    padding-bottom: 7px;

    font-size: 16px;

    border-bottom: 1px solid $border-grey;
  }
}

.catalog__value {
  text-align: right;
}
