.contacts__adress {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: auto;
  margin: 0 6.25%;
  padding: 30px 0 25px 0;

  @media (min-width: $tablet-width) {
    margin: 0 3.9%;
    padding: 63px 5.3% 57px 5.3%;
  }

  @media (min-width: $desktop-width) {
    position: relative;
    top: 95px;
    left: -325px;
    z-index: 1;

    display: flex;
    justify-content: space-between;
    width: 570px;
    margin: auto;
    padding: 65px 89px 56px 76px;

    background-color: $white;
  }
}

.contacts__title {
  width: 50%;

  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  font-family: $font-stack;
  text-align: left;
  color: $titel-lightblack;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    width: 25%;

    font-size: 20px;
    line-height: 25px;
  }

  @media (min-width: $desktop-width) {
    width: 40%;
  }
}

.contacts__text {
  width: 50%;

  font-size: 14px;
  line-height: 20px;
  text-align: left;
  color: inherit;

  @include header-reset;

  @media (min-width: $tablet-width) {
    width: 25%;

    font-size: 16px;
    text-align: right;
  }

  @media (min-width: $desktop-width) {
    width: 38.4%;

    text-align: left;
  }
}

.contacts__text span {
  @media (min-width: $tablet-width) {
    display: block;
    margin-top: 20px;
  }
}

.contacts__map {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    top: 140px;
    left: calc(50% - 53px / 2);

    width: 55px;
    height: 53px;

    background: url("../img/map-pin.png");
    background-repeat: no-repeat;
    background-size: contain;

    @media (min-width: $tablet-width) {
      top: 146px;
      left: calc(50% - 124px / 2);

      width: 124px;
      height: 124px;
    }

    @media (min-width: $desktop-width) {
      top: 150px;
      left: calc(73.6% - 124px / 2);
    }
  }

  @media (min-width: $desktop-width) {
    margin-top: -201px;
  }
}

.contacts__map iframe {
  position: absolute;

  display: block;
  width: 100%;
  height: 363px;

  border: none;

  @media (min-width: $tablet-width) {
    height: 396px;
  }

  @media (min-width: $desktop-width) {
    height: 400px;
  }
}

.contacts__map-paste {
  display: block;
  width: 100%;
  height: 363px;

  @media (min-width: $tablet-width) {
    height: 396px;
  }

  @media (min-width: $desktop-width) {
    height: 400px;
  }
}
