.site-list {
  position: relative;

  @include list-reset;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
  }
}

.site-list__item {
  &::before {
    @media (max-width: $mobile-width-only) {
      content: "";
      position: absolute;
      left: 0;

      width: 100%;

      border-top: 1px solid $border-form;
    }
  }
}

.site-list__item-link {
  display: block;
  padding: 24px 0;

  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  font-family: $font-stack;
  text-align: center;
  color: $titel-lightblack;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    padding: 42px 0 7px 0;

    font-size: 18px;
    line-height: 25px;
    color: $titel-black-two;

    &--active {
      border-bottom: 2px solid $green;
    }
  }

  @media (min-width: $desktop-width) {
    padding: 73px 0 8px 0;

    font-weight: normal;
    font-size: 20px;
    line-height: 25px;
    color: $white;

    &--active {
      border-bottom: 2px solid $white;
    }
  }
}

.site-list__item-link[href] {
  outline: none;

  &:hover {
    opacity: 0.6;
  }

  &:focus,
  &:active {
    opacity: 0.3;
  }
}
