.page-header {
  background-color: $white;
  border-bottom: 1px solid $border-form;

  @media (min-width: $tablet-width) {
    border: 0;
  }

  @media (min-width: $desktop-width) {
    border: 0;
  }
}

.page-header__wrapper {
  display: flex;
  flex-direction: column;

  width: auto;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    max-width: 1300px;
    margin: 0 auto;
  }
}

.page-header__logo {
  width: 191px;
  height: 65px;

  margin-left: 6.25%;

  @media (min-width: $tablet-width) {
    width: 173px;
    height: 82px;

    margin-left: 3.9%;
  }

  @media (min-width: $desktop-width) {
    width: 202px;
    height: 113px;
    margin-left: 3.1%;
  }
}

.page-header__logo-image {
  margin-top: 11px;

  width: 191px;
  height: 39px;

  cursor: auto;

  @media (min-width: $tablet-width) {
    display: block;
    margin-top: 24px;

    width: 173px;
    height: auto;
  }

  @media (min-width: $desktop-width) {
    margin-top: 54px;
    width: 202px;
  }
}
