.social__list {
  display: flex;
  flex-direction: row;
  justify-content: center;

  @include list-reset(
  $list-style-type: none,
  $margin: 0,
  $padding: 25px 10px 14px 0
  );

  @media (min-width: $tablet-width) {
    padding: 0;
  }
}

.social__link {
  svg {
    fill: $svg-lightgrey;
  }

  &:hover svg {
    fill: $green;
  }

  &:active svg,
  &:focus svg {
    opacity: 0.3;

    fill: $green;
  }

  &--vkontakte svg {
    width: 22px;
    height: 12px;

    @media (min-width: $tablet-width) {
      width: 30px;
      height: 16px;
    }
  }

  &--instagram svg {
    width: 16px;
    height: 16px;

    @media (min-width: $tablet-width) {
      width: 23px;
      height: 23px;
    }
  }

  &--facebook svg {
    width: 10px;
    height: 19px;

    @media (min-width: $tablet-width) {
      width: 14px;
      height: 28px;
    }
  }
}

.social__item {
  &:nth-child(2) {
    margin: 0 40px;
  }

  @media (min-width: $tablet-width) {
    &:nth-child(1) {
      margin-top: 9px;
      margin-left: 16px;
    }

    &:nth-child(2) {
      margin: 6px 28px 0 28px;
    }
  }
}
