.page-footer {
  background-color: $bg-white-three;
}

.page-footer__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: auto;
  margin: 0 6.25%;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    align-items: center;
    margin: 0 3.9%;
  }

  @media (min-width: $desktop-width) {
    max-width: 1300px;
    margin: 0 auto;
    padding: 0 40px;
  }
}

.page-footer__logo {
  padding: 38px 0 27px 0;

  text-align: center;

  &:hover img {
    opacity: 0.8;
  }

  &:active img,
  &:focus img {
    opacity: 0.6;
  }

  @media (min-width: $tablet-width) {
    padding: 63px 0 58px 0;
  }
}

.page-footer__social {
  width: auto;
  margin: 0;

  border-top: 1px solid $border-lightgrey;
  border-bottom: 1px solid $border-lightgrey;

  @media (min-width: $tablet-width) {
    border: 0;
  }
}

.page-footer__copyright {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: auto;
  padding: 31px 0;

  @media (min-width: $tablet-width) {
    width: 162px;
    margin: 0;
  }
}
