.page-form__contacts {
  background-color: $bg-white-three;
}

.page-form__item-link {
  @media (min-width: $tablet-width) {
    &--active {
      border-bottom: 2px solid #68b738;
    }
  }

  @media (min-width: $desktop-width) {
    color: $titel-lightblack;
  }
}

.page-form__logo-image {
  cursor: pointer;

  &:hover {
    opacity: 80%;
  }

  &:active,
  &:focus {
    opacity: 60%;
  }
}
