.case {
  position: relative;
}

.case__wrapper {
  display: flex;
  flex-direction: column;

  @media (max-width: $mobile-width-only) {
    background-color: $bg-white-three;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 1300px;
    margin: 0 auto;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      z-index: -1;

      width: 100%;
      height: 463px;

      background: linear-gradient(to right, $bg-white-three 72.6%, $bg-white-six 27.4%);
    }
  }
}

.case__header,
.case__example,
.case__image {
  background-color: $bg-white-three;
}

.case__header {
  @media (min-width: $tablet-width) {
    background-color: $white;
  }

  @media (min-width: $desktop-width) {
    max-width: 1300px;
    margin: 0 auto;
  }
}

.case__example {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: auto;
  margin: 0 6.25%;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
    margin: 38px 3.9% 246px 3.9%;

    background-color: $white;
  }

  @media (min-width: $desktop-width) {
    flex-direction: column;
    width: 43.84%;
    margin: 0 3.07%;
    margin-top: 55px;
    margin-bottom: 70px;

    background-color: transparent;
  }
}

.case__header h2 {
  width: auto;
  margin: 0 6.25%;
  padding-top: 17px;
  padding-bottom: 10px;

  font-weight: normal;
  font-size: 36px;
  line-height: 60px;
  font-family: $font-stack;
  color: $titel-lightblack;

  @media (min-width: $tablet-width) {
    width: auto;
    margin-top: 26px;
    margin-right: 3.91%;
    margin-left: 3.91%;

    font-size: 60px;
  }

  @media (min-width: $desktop-width) {
    width: auto;
    margin: 65px 3.07%;
  }
}

.case__description {
  padding-right: 5px;

  @media (min-width: $tablet-width) {
    padding-right: 20px;

    font-size: 16px;
    line-height: 24px;
  }

  @media (min-width: $desktop-width) {
    z-index: 0;

    margin-bottom: 41px;
    padding-right: 135px;
  }
}

.case__list {
  display: flex;
  flex-grow: 1;
  flex-direction: row;
  justify-content: space-between;

  @include list-reset(
  $list-style-type: none,
  $margin: 10px 0 20px 0,
  $padding: 0
  );

  @media (min-width: $tablet-width) {
    max-height: 72px;
    margin-top: 37px;
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    margin-top: 28px;
  }
}

.case__item {
  position: relative;

  width: 124px;
  height: 56px;
  padding: 13px 0 17px 0;

  font-weight: normal;
  font-size: 24px;
  line-height: 24px;
  font-family: $font-stack;
  text-align: center;
  text-transform: uppercase;

  border: 1px solid $border-grey;
  border-radius: 4px;

  @media (min-width: $tablet-width) {
    width: 162px;
    height: 72px;
    padding-bottom: 20px;

    font-size: 30px;
    line-height: 37px;

    .case__legend {
      top: 12px;

      width: 114px;
    }

    &:last-child {
      margin-right: 29.33%;

      .case__legend {
        width: 142px;
      }
    }
  }

  @media (min-width: $desktop-width) {
    width: 28.4%;
    max-height: 72px;

    font-size: 30px;
    line-height: 37px;

    &:last-child {
      margin-right: 35.8%;

      .case__legend {
        width: 142px;
      }
    }
  }
}

.case__legend {
  position: relative;
  top: 9px;

  display: block;
  width: 87px;
  margin: 0 auto;
  padding: 0 5px;

  font-size: 12px;
  line-height: 12px;
  font-family: "Arial", sans-serif;
  text-transform: lowercase;

  background-color: $bg-white-three;

  @media (min-width: $tablet-width) {
    font-size: 14px;
    line-height: 14px;

    background-color: $white;
  }

  @media (min-width: $desktop-width) {
    background-color: $bg-white-three;
  }
}

.case__sub-title {
  display: flex;
  flex-direction: row;
  justify-content: center;

  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  font-family: $font-stack;
  text-align: center;
  text-transform: uppercase;

  span {
    width: auto;
  }

  @media (min-width: $tablet-width) {
    flex-direction: column;
    width: 26.9%;
    margin-top: 48px;

    font-size: 20px;
    text-align: right;

    span {
      margin-top: 10px;
    }
  }

  @media (min-width: $desktop-width) {
    z-index: 0;

    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    margin-top: 60px;
    margin-bottom: 5px;

    text-align: left;

    span {
      margin-top: 0;
      margin-right: 243px;
    }
  }
}

.case__image {
  padding-top: 20px;

  @media (min-width: $tablet-width) {
    background: linear-gradient(to right, $bg-white-three 50%, $bg-white-six 50%);
  }

  @media (min-width: $desktop-width) {
    width: 43.84%;
    margin: 0 3.07%;
  }
}
