.additional {
  &::before {
    content: "";
    position: relative;
    top: 42px;

    display: block;
    width: 100%;
    height: 3px;

    background-image: linear-gradient(90deg, $white, $white, $green 267px);
  }

  @media (min-width: $tablet-width) {
    margin-top: 34px;

    &::before {
      top: 55px;

      background-image: linear-gradient(90deg, $green 3.64%, $green 441px);
    }
  }

  @media (min-width: $desktop-width) {
    margin-top: 46px;

    &::before {
      top: 54px;

      background-image: linear-gradient(90deg, $green 70px, $green 400px);
    }
  }
}

.additional__wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: auto;
  max-width: $desktop-width;
  margin: 0 6.25%;

  @media (min-width: $tablet-width) {
    margin: 0 3.9%;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    flex-wrap: wrap;
    width: 1300px;
    margin: 0 auto;
    padding: 0 40px;

    &::after {
      content: "";
      position: absolute;
      top: 50px;
      left: 0;

      width: 42px;
      height: 5px;

      border-left: 42px solid $white;
    }
  }
}

.additional__title {
  position: relative;

  width: 246px;
  margin-top: 25px;
  margin-bottom: 32px;
  padding-right: 20px;

  font-weight: normal;
  font-size: 24px;
  line-height: 24px;
  font-family: $font-stack;
  color: $titel-lightblack;

  background-color: $white;

  @media (min-width: $tablet-width) {
    width: 411px;
    margin-top: 30px;
    margin-bottom: 80px;
    padding: 0;
    padding-right: 37px;
    padding-left: 36px;

    font-size: 36px;
    line-height: 36px;

    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
      width: 412px;
    }
  }

  @media (min-width: $desktop-width) {
    width: 376px;
    padding: 0;
    padding-right: 38px;

    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
      width: 378px;
    }
  }
}

.additional .container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @media (min-width: $desktop-width) {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.additional__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @include list-reset;

  @media (min-width: $tablet-width) {
    width: 100%;
  }

  @media (min-width: $desktop-width) {
    width: 73.36%;
    margin-bottom: 85px;
  }
}

.additional__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 2px;

  border-top: 1px solid $border-grey;

  &:last-child {
    border-bottom: 1px solid $border-grey;
  }

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    margin: 0;
    padding: 14px 37px;

    border: 1px solid $border-lightgrey;
    border-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    padding: 16px 0 17px;

    border-top: 1px solid #d9d9d9;
    border-right: 0;
    border-bottom: 0;
    border-left: 0;
  }
}

.additional__item-title {
  margin: 13px 0 10px;

  font-weight: normal;
  font-size: 16px;
  line-height: 16px;
  font-family: $font-stack;
  color: $titel-black-two;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    width: 316px;

    font-size: 20px;
    line-height: 20px;
  }

  @media (min-width: $desktop-width) {
    flex-grow: unset;
    width: 245px;
    margin-top: 8px;
    margin-right: 20px;
  }
}

.additional__specifications {
  width: 100%;

  border-spacing: 0 3px;

  @media (min-width: $tablet-width) {
    width: auto;
    margin-top: 8px;
  }

  @media (min-width: $desktop-width) {
    width: 290px;
    margin-left: 25px;
  }
}

.additional__field,
.additional__value {
  padding: 0;

  font-weight: inherit;
  font-size: inherit;
  line-height: 14px;
  font-family: inherit;
  color: $text-darkgrey;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 20px;
  }
}

.additional__field {
  width: 150px;

  text-align: left;

  @media (min-width: $desktop-width) {
    width: 244px;

    text-align: left;
  }
}

.additional__value {
  text-align: right;

  @media (min-width: $tablet-width) {
    width: 95px;
  }

  @media (min-width: $desktop-width) {
    width: 45px;

    text-align: left;

    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
      width: 65px;
    }
  }
}

.additional__cart-link {
  width: 100%;
  margin: 12px 0 15px;
  padding: 10px 0;

  font-size: 16px;
  line-height: 20px;

  @media (min-width: $tablet-width) {
    flex-grow: 1;
    margin: 14px 0 13px;
  }

  @media (min-width: $desktop-width) {
    flex-grow: unset;
    width: 245px;
    margin: 0;
    margin-top: -2px;
  }
}

.additional__gift {
  margin: 41px 0 35px;

  @media (min-width: $tablet-width) {
    max-height: 200px;
    margin-top: 69px;
    margin-bottom: 76px;
  }

  @media (min-width: $desktop-width) {
    width: $desktop-width/4 - 60px;
    margin-top: 0;
  }
}
