.promo {
  position: relative;

  margin-bottom: 22px;

  @media (min-width: $tablet-width) {
    margin-bottom: -49px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 86px;
  }
}

.promo__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: auto;
  margin-top: 27px;

  @media (min-width: $tablet-width) {
    align-items: flex-start;
    margin-left: 15.63%;
  }

  @media (min-width: $desktop-width) {
    align-items: flex-start;
    max-width: 1300px;
    margin: 30px auto -286px auto;
    padding: 0 121px;
  }
}

.promo::before {
  content: "";
  position: absolute;
  top: -27px;
  z-index: -1;

  width: 100%;
  height: 305px;
  margin-bottom: -305px;

  background-color: #63a73a;
  background-image: url("../img/index-background-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: top;
  background-size: contain;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/index-background-mobile@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    display: none;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    top: -143px;
    left: 50%;
    z-index: auto;

    display: block;
    width: 50%;
    height: 696px;

    background-image: url("../img/index-background-desktop@1x.jpg");
    background-size: 720px auto;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/index-background-desktop@2x.jpg");
    }
  }
}

.promo__header {
  width: 280px;

  font-weight: normal;
  font-size: 36px;
  line-height: 36px;
  font-family: $font-stack;
  text-align: center;
  color: $white;

  @include header-reset;

  @media (min-width: $tablet-width) {
    width: 427px;
    margin-top: 31px;

    font-size: 60px;
    line-height: 60px;
    text-align: left;
    color: $titel-lightblack;
  }

  @media (min-width: $desktop-width) {
    width: 1220px/2 - 80px;
    margin-top: 80px;
  }
}

.promo__slogan {
  margin: 25px 0 27px 0;

  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  font-family: $font-stack;
  text-align: center;
  color: $white;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    margin: 42px 0 47px 0;

    font-size: 20px;
    line-height: 20px;
    color: $titel-lightblack;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 52px;
  }
}

.promo__image {
  @media (min-width: $tablet-width) {
    position: relative;
    left: -120px;

    order: 4;
    width: 709px;
    margin: 0 auto;
    margin-top: -35px;
  }

  @media (min-width: $desktop-width) {
    top: -287px;
    left: 165px;
  }
}

.promo__image-paste {
  @media (min-width: $tablet-width) {
    position: relative;
    top: -45px;
    left: 29px;

    width: auto;
  }
}

.promo__get-program {
  z-index: 1;

  width: 87.5%;
  margin: 0 6.25%;
  padding: 11px 0;

  @media (min-width: $tablet-width) {
    width: 245px;
    margin: 0;
    padding: 16px 26px;

    font-size: 20px;
    line-height: 20px;
  }
}
