.gift {
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 289px;

  background-color: $green;
  background-image: url("../img/gift-photo-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/gift-photo-mobile@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    width: 100%;

    background-image: url("../img/gift-photo-tablet@1x.jpg");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/gift-photo-tablet@2x.jpg");
    }
  }

  @media (min-width: $desktop-width) {
    width: 20.1%;
    max-height: 287px;

    background-image: url("../img/gift-photo-desktop@1x.jpg");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/gift-photo-desktop@2x.jpg");
    }
  }
}

.gift svg {
  position: absolute;
  top: 65px;
  left: calc(50% - 60px / 2);

  width: 60px;
  height: 60px;

  fill: $white;

  @media (min-width: $tablet-width) {
    left: calc(16.7% - 60px / 2);
  }

  @media (min-width: $desktop-width) {
    left: calc(50% - 60px / 2);
  }
}

.gift__description {
  width: 160px;
  margin: 187px auto auto;

  font-size: 16px;
  line-height: 20px;
  font-family: inherit;
  text-align: center;
  color: $white;

  @media (min-width: $tablet-width) {
    margin: auto 125px auto auto;

    text-align: left;
  }

  @media (min-width: $desktop-width) {
    margin: auto 49px 42px 49px;

    text-align: center;
  }
}
